import React, { Component } from 'react';
import { ScrollableLink } from 'react-update-url-on-scroll';

class primarynav extends Component {
  render() {
	const navs = [
			{
				label: 'Business Units',
				link: 'business-units'
			},
			{
				label: 'Awards',
				link: 'awards'
			},
			{
				label: 'Offices',
				link: 'offices'
			},
			{
				label: 'Directors',
				link: 'directors'
			},
			{
				label: 'Contact us',
				link: 'contact-us'
			}
		];
    return (
	<React.Fragment>
		<ul className="flex flex-row justify-end space-x-10 text-sm tracking-wide ">
			{navs.map((value, index) => {
				return <ScrollableLink href={"#" + value.link} key={index}><li><span className="cursor-pointer text-gray-800 text-opacity-75 hover:text-gray-900 hover:text-opacity-100">{value.label}</span></li></ScrollableLink>
			})}
		</ul>
	</React.Fragment>
    )
  }
}

export default primarynav
